    //   min-height: 1px;
    // }
  }

  @include respond(phone) {
    // .carousel-item {
    //   height: 40rem;
    //   min-height: 1px;
    // }
  }
}

.carousel {
  &-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &-overlay {
    position: relative;
    // max-width: 850px
    top: 471px;
    width: 100%;
    @include margin-auto;

    &-wrapper {
      color: #fff;
      z-index: 3;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;

      &::before {
        content: "";
        @include absoldefault;
        background-color: #000;
        opacity: .49;
      }

      >.container {
        z-index: 2;
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }

    &-wrapper.text-center & {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }

    &-wrapper.text-right &-inner {
      a {
        margin-left: 2rem;
      }
    }

    &-wrapper.text-right & {
      order: 2;
      margin-left: auto;
    }

    &-inner {
      // padding: 3rem;
      // @include unselect;

      >* {
        overflow: hidden;
      }

    }

  }

  &-cont {
    &-title {
      &-1 {
        font-size: 81px;
        font-weight: 500;
        margin-bottom: 10px;
      }

      &-2 {
        position: relative;
        font-size: 40px;
        font-weight: 500;

        &::after {
          content: "";
          display: block;
          width: 944px;
          @include margin-auto;
          margin-top: 25.7px;
          margin-bottom: 43px;
          border-bottom: 1px solid #fff;
        }
      }

    }

    &-sub {
      font-size: 28px;
      font-weight: 500;
    }

  }

  &-indicators {
    width: 100%;
    flex-wrap: wrap;
    margin: 0;
    bottom: 3rem;
    left: 50%;
    right: auto;
    text-align: center;
    transform: translateX(-50%);

    li {
      display: inline-block;
      width: 1.2rem;
      height: 1.2rem;
      border-radius: 0;
      background-color: transparent;
      border: 2px solid #fff;
      margin: 0 0.75rem;
      transition: all 0.3s ease;

      &.active {
        width: 3rem;
        background-color: #fff;
      }
    }
  }

  &-control {

    &-prev,
    &-next {
      width: 47px;
      height: 47px;
      opacity: 1;
      top: 532px;
      color: #fff;
      border-radius: 50%;
      // background-color: rgba(#000, 0);
      // border: 1px solid #fff;
      opacity: 1;

      &:hover {
        background-color: rgba(#000, 0.75);
        opacity: 1;
      }

      i {
        font-size: 1.25rem;
      }
    }

    &-prev {
      left: 2rem;
    }

    &-next {
      right: 2rem;
    }
  }

  @media (max-width: 1700px) {
    &-cont {
      &-title {
        &-1 {
          font-size: 38px;
        }

        &-2 {
          font-size: 27px;
        }

        &-sub {
          font-size: 20px;
        }
      }
    }

    &-hero {
      width: 700px;
    }
  }

  @media (max-width: 1400px) {
    &-cont {
      &-title {
        &-1 {
          font-size: 32px;
        }

        &-2 {
          font-size: 25px;
        }

      }

      &-sub {
        font-size: 18px;
      }

      .link-btn {
        font-size: 18px;
      }
    }

    &-hero {
      width: 600px;
      right: -200px;
    }
  }

  @media (max-width: 1200px) {
    &-hero {
      width: 450px;
      right: -140px;
    }
  }

  @media (max-width: 991px) {
    &-control {

      &-prev,
      &-next {
        width: 3rem;
        height: 3rem;
      }
    }

    &-hero {
      width: 450px;
      right: -140px;
      bottom: -180px;
      top: auto;
      transform: none;
    }
  }

  @media (max-width: 767px) {
    &-cont {
      &-title {
        &-1 {
          font-size: 22px;
        }

        &-2 {
          font-size: 18px;
          margin-bottom: 28px;
        }
      }

      &-sub {
        font-size: 15px;
        margin-bottom: 25px;
      }
    }

    &-hero {
      width: 400px;
    }
  }

  @media (max-width: 567px) {
    &-cont {
      &-title {
        &-1 {
          font-size: 13px;
          margin-bottom: 5px;
        }

        &-2 {
          font-size: 10px;
          margin-bottom: 10px;
        }
      }

      &-sub {
        margin-bottom: 12px;
      }

      .link-btn {
        font-size: 12px;
        padding: 8px 18px;

        img {
          width: 10px;
          height: 10px;
        }
      }
    }

    &-hero {
      width: 230px;
      bottom: -120px;
      right: -75px;
    }

    &-control-prev,
    &-control-next {
      display: none;
    }
  }
}